.nav-menu{
    display: flex;
}
.nav-menu li{
    padding: 0 1rem;
}
.nav-menu li a{
    text-decoration: none;
    font-size: 1.2rem;
    font-weight: 500;
}
.Header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: 0.5s;
}
.hamburger{
    display: none;
}
@media screen and (max-width:1040px) {
    
    .nav-menu{
        flex-direction:column ; 
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0,0.9);
        position: absolute;
        
        left: -100%;
        transition: 0.3s;
        z-index: -3;
    }
    .nav-menu .active{
        flex-direction:column ; 
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0,0.9);
        position: absolute;
        
        left: 0;
        transition: 0.3s;
        z-index: -3;
    }

    .nav-menu li {
        padding: 1rem 0;
    }
    .nav-menu li a{
        font-size: 2rem;
    }
    .hamburger{
        display: block;
    }
}
